@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Nunito:ital,wght@0,300;0,600;0,700;0,900;1,300;1,600;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,500;0,700;0,900;1,200;1,300;1,500;1,700;1,900);
.heading-1 {
  font-size: 4rem;
  /* font-style: italic; */
  font-family: var(--font-title);
  font-weight: 500;
  display: inline-block;
  text-transform: uppercase;
  padding: 1rem 8rem;
  letter-spacing: 2px;
  text-align: center;
  margin-bottom: 2.5rem;
  color: var(--text-light);
  position: relative;
  border-radius: 5px;
  background-image: linear-gradient(rgba(0, 0, 111, 0.8), rgba(0, 0, 111, 0.8)),
    url(/portfolio/static/media/bg-dots.fa2cb267.png);
  background-size: cover;
  background-position: center center;
}

.link:link,
.link:visited {
  font-family: inherit;
  text-decoration: none;
  display: inline-block;
  color: var(--color-primary-light);
}

:root {
  --color-primary: rgb(44, 49, 214);
  --color-primary-light: rgb(0, 113, 250);
  --color-primary-light-2: rgb(79, 158, 255);
  --color-primary-dark: rgb(0, 0, 111);
  --text-dark: rgb(0, 3, 20);
  --text-light: rgb(230, 255, 255);
  --font-text: "Montserrat", sans-serif;
  --font-title: "Montserrat", sans-serif;
  --font-navbar: "Nunito", sans-serif;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  font-family: var(--font-text);
  font-weight: 300;
}

.container {
  display: grid;
  grid-template-columns:
    [sidebar-start] 20rem [sidebar-end full-start] minmax(6rem, 1fr)
    [center-start] repeat(8, [col-start] minmax(-webkit-min-content, 14rem) [col-end])
    [center-end] minmax(6rem, 1fr) [full-end];
  grid-template-columns:
    [sidebar-start] 20rem [sidebar-end full-start] minmax(6rem, 1fr)
    [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end])
    [center-end] minmax(6rem, 1fr) [full-end];
  grid-template-rows: repeat(5, minmax(1fr, -webkit-max-content));
  grid-template-rows: repeat(5, minmax(1fr, max-content));
}

.about {
  grid-column: full-start 1 / full-end;
}

.technologies {
  /* background-color: lawngreen; */
  grid-column: col-start 1 / col-end 8;
  margin-top: -10vh;
  padding: 15vh 0;
}

.projects {
  grid-column: full-start 1 / full-end;
  margin-top: -10vh;
}

.contact {
  grid-column: full-start 1 / full-end;
  padding: 5rem;
}

.footer {
  grid-column: full-start 1 / full-end;
}

@media (max-width: 900px) {
  html {
    font-size: 55%;
  }
}

@media (max-width: 700px) {
  .container {
    grid-template-columns:
      [full-start] minmax(6rem, 1fr)
      [center-start] repeat(8, [col-start] minmax(-webkit-min-content, 14rem) [col-end])
      [center-end] minmax(6rem, 1fr) [full-end];
    grid-template-columns:
      [full-start] minmax(6rem, 1fr)
      [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end])
      [center-end] minmax(6rem, 1fr) [full-end];
    grid-template-rows: repeat(6, -webkit-max-content);
    grid-template-rows: repeat(6, max-content);
  }
}

.header__container {
  grid-column: sidebar-start / sidebar-end;
  grid-row: 1 / 6;
  /* background-color: var(--color-primary-dark); */
  background-image: linear-gradient(rgba(0, 0, 111, 0.8), rgba(0, 0, 111, 0.8)),
    url(/portfolio/static/media/bg-dots.fa2cb267.png);
  background-size: contain;
  font-size: 2rem;
  position: relative;
  font-family: var(--font-navbar);
  font-weight: bold;
  letter-spacing: 2px;
}

.header__content {
  padding: 1.5rem;
  width: 20rem;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  color: var(--text-light);
}

.header__logo {
  margin-top: 10px;
  width: 8rem;
  height: 6rem;
  stroke-width: 2px;
  color: var(--text-light);
  z-index: 1000;
}

.nav {
  text-transform: capitalize;
  margin-bottom: auto;
  margin-top: 3rem;
  list-style: none;
}
.nav__item {
  color: inherit;
  cursor: pointer;
  position: relative;
  text-align: center;

  z-index: 1;
}

.nav__link:link,
.nav__link:visited {
  transition: all 0.5s;
  text-decoration: none;
  font-size: inherit;
  color: inherit;
  width: 100%;
  display: block;
  height: 100%;
  padding: 1rem 1.5rem;
}

.nav__item.active {
  color: var(--color-primary);
}

.nav__item:not(:last-child) {
  margin-bottom: 1rem;
}
.nav__item::before,
.nav__item::after {
  z-index: -1;
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  transition: transform 0.5s ease-out;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transform: scaleX(0);
}
.nav__item::before {
  top: 0;
  height: 95%;
  transform-origin: right;
  background-color: var(--text-light);
  /* background-image: linear-gradient(
			rgba(255, 255, 255, 0.8),
			rgba(255, 255, 255, 0.8)
		),
		url(../img/bg-dots.png);
	background-size: revert; */
}
.nav__item::after {
  bottom: 0;
  height: 5%;
  transform-origin: left;
  background-color: var(--color-primary-light);
}

.nav__item.active::before {
  transition: transform 0.5s ease-in;
  transform-origin: left;
  transform: scaleX(1);
  -webkit-animation: bgMove 40s linear infinite;
          animation: bgMove 40s linear infinite;
}

.nav__item.active::after {
  transition: transform 0.5s ease-in;
  transform-origin: right;
  transform: scaleX(1);
}

.social-media {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  margin-bottom: 10px;
}

.social-media a:link,
.social-media a:visited {
  color: inherit;
}

.social-media__icon {
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  transition: all 0.2s;
}

.social-media__icon:hover {
  transform: translateY(-5px) scale(1.1);
}

@-webkit-keyframes bgMove {
  0% {
    background-position: right;
  }
  100% {
    background-position: left;
  }
}

@keyframes bgMove {
  0% {
    background-position: right;
  }
  100% {
    background-position: left;
  }
}

@media only screen and (min-width: 701px) {
  .nav__item:hover::before {
    transition: transform 0.5s ease-in;
    transform-origin: left;
    transform: scaleX(1);
    -webkit-animation: bgMove 40s linear infinite;
            animation: bgMove 40s linear infinite;
  }

  .nav__item:hover::after {
    transition: transform 0.5s ease-in;
    transform-origin: right;
    transform: scaleX(1);
  }

  .nav__link:hover {
    color: var(--color-primary);
  }
}

.burger {
  grid-column: 8/9;
  grid-row: 1/2;
  display: none;
}

.burger > * {
  width: 5rem;
  height: 4px;
  border-radius: 15px;
  background-color: white;
  transition: all 0.5s;
}

.burg2 {
  margin: 1rem 0;
}

.burger.active .burg1 {
  transform: rotate(137deg) translate(9px, -12px);
}

.burger.active .burg2 {
  transform: translateX(-100%);
  opacity: 0;
}

.burger.active .burg3 {
  transform: rotate(-137deg) translate(7px, 8px);
}

@media only screen and (max-width: 700px) {
  .header__container {
    grid-column: full-start / full-end;
    grid-row: 1 / 2;
    position: fixed;
    width: 100%;
    z-index: 1000;
  }

  .header__content {
    padding: 1.5rem;
    width: 100%;
    position: relative;
    display: grid;
    align-items: center;
    justify-items: center;
    grid-template-columns: repeat(8, 1fr);
    grid-row-gap: 1rem;
  }

  .burger {
    display: inline-block;
  }

  .nav {
    display: flex;
    font-size: 3rem;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: calc(100% - 85.6px);
    margin-top: 0;
    background-color: rgba(0, 0, 111, 0.9);
    transition: all 0.5s;
    transform: translateX(-100%);
  }

  .nav.active {
    transform: translateX(0);
  }

  .nav__item {
    width: 35rem;
  }

  .social-media {
    grid-column: 6 / 8;
    margin-bottom: 0;
  }

  .social-media a {
    display: flex;
    align-items: center;
  }

  .header__logo {
    margin-top: 0;
  }

  .nav__item:not(:last-child) {
    margin-bottom: 5rem;
  }
}

.footer-container {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  padding: 2rem 4rem;
  background-image: linear-gradient(
      rgba(145, 145, 145, 0.253),
      rgba(145, 145, 145, 0.253)
    ),
    url(/portfolio/static/media/footer-bg.90c954e1.jpg);
  background-size: 25%;
  background-position: center;
  color: white;
  font-size: 1.2rem;
}

.about__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
  clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
  background-image: linear-gradient(
      rgba(239, 239, 255, 0.911),
      rgba(239, 239, 255, 0.904)
    ),
    url(/portfolio/static/media/about-bg-3.d583f0bf.jpg);

  background-size: cover;
  background-position: left;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  padding: 10rem 10rem 15rem 10rem;
}

.about__name {
  position: relative;
}

.about__name::before {
  content: "My name is";
  color: rgb(122, 122, 122);
  position: absolute;
  top: -5rem;
  left: 0;
  padding: 1rem;
  font-size: 2rem;
}

.about__text {
  font-size: 3.5rem;
  width: 80%;
  display: inline;
  line-height: 2;
  text-align: justify;
  font-family: inherit;
  margin: 4rem 0;
}

.about__text span {
  font-family: inherit;
  font-weight: 900;
  padding: 0.5rem 0;
  color: rgba(59, 59, 255, 0.8);
  background-position: center center;
}

.about__resume {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about__resume-link:link,
.about__resume-link:visited {
  font-size: 2rem;
}

.resume-icon {
  width: 2.5rem;
  height: 2.5rem;
  margin-right: 1.5rem;
}

@media (max-width: 1200px) {
  .about__container {
    padding: 8rem 5rem 15rem 5rem;
  }

  .about__text {
    width: 90%;
  }
}

@media (max-width: 700px) {
  .about__container {
    margin-top: 9.6rem;
  }
}

@supports ((-webkit-background-clip: text) or (background-clip: text)) {
  .about__text span {
    background-image: linear-gradient(
        rgba(59, 59, 255, 0.8),
        rgba(59, 59, 255, 0.8)
      ),
      url(/portfolio/static/media/bg-dots.fa2cb267.png);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
  }
}

@-webkit-keyframes bgMove {
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}

@keyframes bgMove {
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}

.contact__container {
  /* margin: 5rem; */
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 2rem;
}

.contact-text {
  padding: 1.5rem 3rem;
}

.contact-text:first-of-type {
  margin-top: 2rem;
}

.contact__item {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact__icon {
  width: 2.5rem;
  height: 2.5rem;
  margin-right: 1.5rem;
}

.technologies__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 2rem;
}

.technologies__subtitle {
  font-size: 2.5rem;
  font-family: var(--font-text);
  font-weight: 500;
}

.technologies__subtitle:first-of-type {
  margin-top: 3rem;
}

.technology__list {
  display: grid;
  margin: 3rem 0;
  grid-template-columns: repeat(2, -webkit-max-content);
  grid-template-columns: repeat(2, max-content);
  grid-row-gap: 1rem;
  grid-column-gap: 7rem;
  justify-items: start;
}

.technology__item {
  display: flex;
  justify-content: center;
  align-items: center;
}

.technology__icon {
  width: 2.5rem;
  height: 2.5rem;
  margin-right: 1.5rem;
}

.project-item__container {
  box-sizing: border-box;
  width: 100%;
  max-width: 35rem;
  height: 25rem;
  perspective: 1500px;
  display: inline-block;
  opacity: 1;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.project-item__card {
  width: 100%;
  height: 100%;
  transition: transform 1s ease-in-out;
  transform-style: preserve-3d;
  position: relative;
}

.project-item__card:hover {
  transform: rotateY(180deg);
}

.project-item__card-face {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
  border-radius: 5px;
  box-shadow: 0 3px 10px 3px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
}

.project-item__card-face--front {
  /* background-color: var(--card-bg-border); */
  background-color: white;
  width: 100%;
}

.project-item__card-face--back {
  background-color: white;
  transform: rotateY(180deg);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem;
  color: var(--text-dark);
}

.project-item__description {
  font-size: 2rem;
}

.project-item__technologies {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin-top: auto;
  padding: 1rem;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
}

.project-item__technologies::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: lightgrey;
}

.project-item__technologies::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: lightgrey;
}

.project-item__technology {
  padding: 1rem 2rem;
  margin: 5px 2px;
  border-radius: 100px;
  font-size: 1.4rem;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.project-item__links {
  display: flex;
  width: 100%;
  padding: 1rem 2rem 0 2rem;
  justify-content: space-evenly;
  align-items: center;
}

.project-item__link:link,
.project-item__link:visited {
  font-size: 1.5rem;
  z-index: 3;
}

.project-item__link-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.link-icon {
  height: 2.5rem;
  width: 2.5rem;
  margin-right: 1rem;
}

.project-item__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.project-item__name {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 40%;
  width: 100%;
  background-color: rgba(0, 0, 190, 0.658);
  color: var(--text-light);
  -webkit-clip-path: polygon(0 40%, 100% 0, 100% 100%, 0 100%);
          clip-path: polygon(0 40%, 100% 0, 100% 100%, 0 100%);
  padding-top: 45px;
  padding-right: 30px;
  text-align: end;
  font-size: 2.5rem;
  font-weight: 600;
  font-family: var(--font-title);
  letter-spacing: 2px;
  opacity: 1;
  transition: opacity 0.5s 0.5s, visibility 0s;
}

.project-item__card:hover .project-item__name {
  transition: opacity 1s, visibility 0s 1s;
  opacity: 0;
  visibility: hidden;
}

@media only screen and (max-width: 700px) {
  .project-item__card:hover {
    transform: none;
  }

  .project-item__card:hover .project-item__name {
    transition: none;
    opacity: 1;
    visibility: visible;
  }

  .project-item__card-face--back {
    background-color: white;
    transform: rotateY(0deg);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 2rem;
    color: var(--text-dark);
  }

  .project-item__card-face {
    position: relative;
    width: 100%;
    height: 25rem;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    overflow: hidden;
    border-radius: 0;
    box-shadow: 0 3px 10px 3px rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .project-item__container {
    height: 50rem;
  }

  .project-item__card {
    border-radius: 5px;
    overflow: hidden;
  }
}

.projects__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.541), rgba(0, 0, 0, 0.719)),
    url(/portfolio/static/media/projects-bg.aa7ec1ca.png);
  background-size: cover;
  background-position: center center;
  padding: 16vh 0 8vh 0;
  -webkit-clip-path: polygon(0 15vh, 100% 0, 100% 85vh, 0 100%);
  clip-path: polygon(0 15vh, 100% 0, 100% 100%, 0 100%);
}

.projects__content {
  align-self: stretch;
  padding: 4rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(35rem, 1fr));
  grid-gap: 4rem;
  justify-items: center;
}

.more-soon {
  color: var(--text-light);
  font-size: 1.5rem;
  -webkit-filter: brightness(0.7);
          filter: brightness(0.7);
}

